.profile {
  color: $grey;
  font-size: 14px;

  &__profile-description {
    display: none;
  }

  &__image {
    display: flex;
  }
}

@media (min-width: $desktop-width) {
  .profile {
    display: flex;

    &__profile-description {
      display: block;
      margin-left: 8px;
    }
  }
}
